	line-height: inherit;
	font-style: normal;
	vertical-align: baseline;
	border: 0;
	display: flex;
	align-items: center;
	margin-left: auto;
	margin-right: 0;
	font-family: Gotham, sans-serif;
	font-weight: 400;
}

.q-partners-filter__content {
	visibility: hidden;
	opacity: 0;
	height: 0;
	overflow: hidden;
	transition: visibility 200ms, opacity 200ms;
}

.q-partners-filter__content--active {
	visibility: visible;
	opacity: 1;
	height: auto;
}

.q-partners-filter__subheading {
	padding: 0;
