.q-partners-filter__grid {
	display: flex;
	align-items: stretch;
	flex-wrap: wrap;
	margin-left: -15px;
	margin-right: -15px;
}

.q-partners-filter__item {
	flex: none;
	padding-left: 15px;
	padding-right: 15px;
	width: 50%;
	display: flex;
	align-items: stretch;
	justify-content: center;
	position: relative;
	margin-bottom: 30px;
}

@media (min-width: 480px) {
	.q-partners-filter__item {
		flex: none;
		padding-left: 15px;
		padding-right: 15px;
		width: 33.33333%;
	}
}

@media (min-width: 720px) {
	.q-partners-filter__item {
		flex: none;
		padding-left: 15px;
		padding-right: 15px;
		width: 25%;
	}
}

@media (min-width: 1080px) {
	.q-partners-filter__item {
		flex: none;
		padding-left: 15px;
		padding-right: 15px;
		width: 20%;
	}
}

.q-partners-filter__item::before {
	content: "";
	display: block;
	padding-bottom: 100%;
	background: #F7FAFC;
}

.q-partners-filter__item::after {
	content: "";
	position: absolute;
	top: 0;
	bottom: 0;
	left: 15px;
	right: 15px;
	z-index: 0;
	margin: auto;
	background: #F7FAFC;
	border-radius: 2px;
}

@media (pointer: fine) {
	.q-partners-filter__item:hover::after {
		background: transparent;
		box-shadow: 0 12px 23px 0 rgba(170, 177, 188, 0.2);
	}
}

.q-partners-filter__link {
	display: flex;
	align-items: center;
	flex: 0 1 auto;
	position: relative;
	z-index: 1;
	padding: 0 15px;
}

.q-partners-filter__link img {
	filter: contrast(30%) grayscale(1);
	max-width: 85%;
	height: auto;
	margin-left: auto;
	margin-right: auto;
}

@media (pointer: fine) {
	.q-partners-filter__link:hover > img {
		filter: contrast(1) grayscale(0);
	}
}

.q-partners-filter__modal-wrap {
	display: none;
}

.q-modal__image {
	max-width: 150px;
	height: auto;
	margin: 0 0 20px;
}
.q-modal__website {
	display: inline-block;
	margin: 0 0 20px;
}

